import React, { useState } from 'react';
import { ActivityIndicator, FlatList, Text, View, Image, Button } from 'react-native';
import data from './assets/cards.json'
import { Audio } from 'expo-av';

const FlatListItemSeparator = () => {
  return (
    <View
      style={{
        height: 1,
        width: "100%",
        opacity: 0.25,
        backgroundColor: "#000000",
        marginRight: 8
      }}
    />
  );
}

const SoundButton = (props) => {
  async function playSound(cardID, sound) {
    const soundObject = await Audio.Sound.createAsync(
      { uri: 'https://hearthstonesounds.s3.amazonaws.com/'+cardID+'_'+sound+'.wav' },
      { shouldPlay: true }
    );
    try {
      await soundObject.playAsync();
      // Your sound is playing!
      } catch (error) {
      // An error occurred!
      }
  }

  return (
    <Button style={{margin: 4}} title={props.title} onPress= {() => {
      playSound(props.cardID, props.sound)
    }}
    />
  )
}

const SmallClassImage = (props) => {
  const cardClass = props.cardClass
  var img = 'class_small_neutral'

  switch (cardClass) {
    case 'DEMONHUNTER':
      img = 'class_small_demonhunter'
      break;
    case 'DRUID':
      img = 'class_small_druid'
      break;
    case 'HUNTER':
      img = 'class_small_hunter'
      break;
    case 'MAGE':
      img = 'class_small_mage'
      break;
    case 'PALADIN':
      img = 'class_small_paladin'
      break;
    case 'PRIEST':
      img = 'class_small_priest'
      break;
    case 'ROGUE':
      img = 'class_small_rogue'
      break;
    case 'SHAMAN':
      img = 'class_small_shaman'
      break;
    case 'WARLOCK':
      img = 'class_small_warlock'
      break
    case 'WARRIOR':
      img = 'class_small_warrior'
      break;

  }


  return (
    <Image style={{width: 34, height: 34}} source={require('./assets/img/'+img+'.png')}/>
  )
}

const YourApp = () => {
  const mydata = data.filter(x => (x.collectible === true && x.type === 'MINION')).slice(0,100)
  const [isLoading, setLoading] = useState(true);
  const [hasScrolled, setHasScrolled] = useState(false);
  const [page, setPage] = useState(1);

  return (
    <View style={{ flex: 1, padding: 24 }}>
    <FlatList
      data={mydata}
      renderItem={({ item }) => (
        <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
        , justifyContent: 'space-between'
        , marginTop: 4, marginBottom: 4, marginRight: 8}}>
          <SmallClassImage cardClass={item.cardClass}/>
          <Text style={{flexGrow: 1}}>{item.name}</Text>
          <SoundButton title='Play' cardID={item.id} sound='P' />
          <View style={{padding: 4}} />
          <SoundButton title='Attack' cardID={item.id} sound='A' />
          <View style={{padding: 4}} />
          <SoundButton title='Death' cardID={item.id} sound='D' />
        </View>
      )}
      ItemSeparatorComponent={FlatListItemSeparator}
    />
    </View>
  );
}

export default YourApp;
